.video__play {
  cursor: pointer;
  position: absolute;
  top: 64px;
  left: 132px;
  width: 100px;
  height: 100px;
  padding: 0;
  border: 0;
  background-color: rgba(255, 255, 255, 0);
  z-index: 2;
  border-radius: 50%;
}
@media (max-width: 1199px) {
  .video__play {
    width: 73px;
    height: 73px;
    top: 47px;
    left: 96px;
  }
}
@media (max-width: 767px) {
  .video__play {
    top: calc((100% - 73px) / 2);
    left: calc((100% - 73px) / 2);
  }
}
.video__play::after {
  position: absolute;
  width: 72px;
  height: 72px;
  content: "";
  top: 14px;
  left: 14px;
  background: linear-gradient(44.89deg, #003d85 25.38%, #b80027 74.89%);
  border-radius: 50%;
  z-index: -1;
}
@media (max-width: 1199px) {
  .video__play::after {
    width: 52px;
    height: 52px;
    top: 10px;
    left: 11px;
  }
}
.video__play::before {
  position: absolute;
  width: 100%;
  height: 100%;
  content: "";
  top: 0;
  left: 0;
  border: 0;
  background: linear-gradient(97.05deg, rgba(237, 2, 51, 0.2) 17.6%, rgba(0, 62, 183, 0.2) 95.31%);
  border-radius: 50%;
  padding: 3px;
  -webkit-mask: linear-gradient(#ffffff 0 0) content-box, linear-gradient(#ffffff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
}
