  .video__play:hover:not(.focus-visible) {
    opacity: 0.7;
    transition: opacity 0.6s ease;
  }
}
.video__play.focus-visible:focus {
  opacity: 0.7;
  transition: opacity 0.6s ease;
}

.video__preview {
  width: 100%;
