.coache {
  max-width: 260px;
  width: 100%;
  max-height: 294px;
  height: 294px;
  color: #ffffff;
  position: relative;
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  padding: 54px 33px 24px;
  transition: all 0.6s ease;
}
@media (max-width: 1199px) {
  .coache {
    max-width: 268px;
  }
}
@media (hover: hover) {
  .coache:hover:not(.focus-visible) {
    flex-direction: column;
  }
  .coache:hover:not(.focus-visible)::after {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(16, 38, 84, 0.89);
    z-index: -1;
  }
  .coache:hover:not(.focus-visible) .coache__info {
    display: block;
  }
  .coache:hover:not(.focus-visible) .coache__position {
    display: inline-block;
  }
}
.coache.focus-visible:focus {
  flex-direction: column;
}
.coache.focus-visible:focus::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(16, 38, 84, 0.89);
  z-index: -1;
}
.coache.focus-visible:focus .coache__info {
  display: block;
}
.coache.focus-visible:focus .coache__position {
  display: inline-block;
}

.coache__name {
  font-family: "Roboto Condensed", "Tahoma", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  margin: 0;
}

.coache__position {
  display: none;
  margin: 0 0 33px;
  text-align: center;
}

.coache__photo {
  position: absolute;
  z-index: -2;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  object-fit: cover;
}
.coache__photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.coache__info {
  display: none;
  overflow-y: scroll;
}

.coache__achievements {
  padding-left: 40px;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 13px;
}

.coache__achievement {
  font-family: "Roboto Condensed", "Tahoma", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
}

